import React, { useState } from 'react';
import axios from 'axios';

import AddSvg from '../../assets/img/icons/add.svg';

const AddTaskForm = ({ list, onAddTask }) => {
  const [visibleForm, setVisibleForm] = useState(false);
  const [inputValue, setInputValue] = useState('');
  const [isLoading, setIsLoading] = useState();

  const toggleFormVisible = () => {
    setVisibleForm(!visibleForm);
    setInputValue('');
  };

  const addTask = () => {
    const obj = {
      listId: list.id,
      text: inputValue,
      completed: false,
    };
    setIsLoading(true);
    axios
      .post('https://my-json-server.typicode.com/sl14888/DB-for-react-TODO/tasks/', obj)
      .then(({ data }) => {
        onAddTask(list.id, data);
        toggleFormVisible();
      })
      .catch(() => {
        alert('   ');
      })
      .finally(() => {
        setIsLoading(false);
      });
  };

  return (
    <div className="tasks__form">
      {!visibleForm ? (
        <div onClick={toggleFormVisible} className="tasks__form-new">
          <img src={AddSvg} alt="add icon" />
          <span> </span>
        </div>
      ) : (
        <div className="tasks__form-block">
          <input
            value={inputValue}
            className="field"
            type="text"
            placeholder=" ..."
            onChange={(e) => setInputValue(e.target.value)}
          />
          <button disabled={isLoading} onClick={addTask} className="btn btn-rest">
            {isLoading ? '...' : ' '}
          </button>
          <button onClick={toggleFormVisible} className="btn btn--grey btn-rest">
            
          </button>
        </div>
      )}
    </div>
  );
};

export default AddTaskForm;
